@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
a,
h1,
i {
  animation: flow 15s ease-in-out infinite;
  background: linear-gradient(-60deg, #c55ecc, #b45abb, #e73c7e, #ee7752);
  background-size: 300%;

  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

input {
  background: none;
  font-weight: bold;
  font-size: 1.3em;
  color: white;
  width: 200px;
  outline: none;
  border: none;
  border-bottom: 3px solid #ffffff;
  height: 25px;
  margin-left: 10px;
}

h2 {
  font-size: 4.7rem;
  font-weight: bold;
}

@keyframes flow {
  0% {
    background-position: 0 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0 50%;
  }
}

.links-con {
  display: flex;
  justify-content: space-evenly;
}

.love-tab-con,
.dice-tab-con,
.laugh-tab-con {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 480px;
}

.fa-grin-hearts,
.fa-dice,
.fa-grin-squint {
  font-size: 6rem;
  cursor: pointer;
  transition: 0.3s ease;
}

.fa-grin-hearts:hover,
.fa-dice:hover,
.fa-grin-squint:hover {
  font-size: 7rem;
}

p {
  font-size: 1.5em;
}

body{
    margin: 0%;
    padding: 0%;
    background-color: #654ea3;
    color: #ffffff;
    font-family: 'Roboto', sans-serif;
}

.header{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
 
}

.header > h1{
    font-size: 3.2rem;

}

.numbergen-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}


label {
    font-weight: bold;
    font-size: 1.8rem;
}

form > input{
    color:  #654ea3;
    font-weight: bold;
    outline: none;
    border: none;
    background: #ffffff;
    width: 30px;
    border-radius: 5px;
    padding: 10px;
}

.numbergen-container > p{
    font-size: 2rem;
    width: 800px;
    height: 200px;
}

#love > h1 {
    font-size: 3rem;
    text-align: center;
}

.fa-kiss-beam{
    font-size: 12rem;
    margin-left: 50px;
    margin-top: 50px;
}

.name-inputs-con {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 130px;
}
.love-names {
  display: flex;
  justify-content: center;
}

.lover-name,
.my-name {
  display: flex;
  align-items: center;
}

.love-result-con {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#try-my-luck {
  display: flex;
  flex-direction: column;
  align-items: center;
  outline: none;
  margin-bottom: 30px;
}

.my-luck-btn {
  font-family: "Roboto", sans-serif;
  outline: none;
  font-size: 1.4rem;
  color: #ffffff;
  border: none;
  background: none;
}

.fa-hand-holding-heart {
  margin-top: 10px;
  font-size: 50px;
  transition: 0.3s ease;
}
.fa-hand-holding-heart:hover {
  font-size: 60px;
}
.love-result {
  font-size: 1.6rem;
}

#click-me {
    background: tomato;
    width: 150px;
    border: 3px solid black;
}

.random-num-display {
    margin-bottom: 25px;
    font-size: 45px;
}

.joke-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
}

.joke-container > form {
  display: flex;
  flex-flow: column nowrap;
  margin-top: 20px;
}

.joke-container > button {
  background: none;
  outline: none;
  color: white;
  padding: 10px;
  border: 1px solid white;
  border-radius: 20px;
  margin-top: 20px;
  cursor: pointer;
  width: 170px;
  height: 40px;
}

#category {
  margin-top: 15px;
  padding: 8px;
  background: none;
  color: white;
  outline: none;
  border: 1px solid white;
  width: 150px;
  align-self: center;
}

#category > option {
  color: white;
  background-color: #654ea3;
  border: 1px solid white;
}

